import { useHistory, withRouter } from "react-router-dom";
import React, {useEffect, useState} from "react";
import './LogIn.scss';
import { authenServices } from "../../services/authenServices";
import { toast } from 'react-toastify';

function LogIn() {

	const [valueAccount, setValueAccount] = useState({username: '', password: ''});
	const [valueSignUp, setValueSignUp] = useState({username: '', email: '', password: ''});
	const history = useHistory();

	useEffect(() => {
		const signUpButton = document.getElementById('signUp');
		const signInButton = document.getElementById('signIn');
		const container = document.getElementById('container');
		signUpButton?.addEventListener('click', () => {
			container.classList.add("right-panel-active");
		});
		
		signInButton?.addEventListener('click', () => {
			container.classList.remove("right-panel-active");
		});
	});

	function handleChangeUsername(e) {
		setValueAccount({...valueAccount, username: e.target.value})
	}

	function handleChangePassword(e) {
		setValueAccount({...valueAccount, password: e.target.value})
	}

	const checkSignIn = () => {
		authenServices.signIn(valueAccount).then(res => {
			history.push('/')
		}).catch(err => {
			toast(err.response.data, {
				position: "top-right",
				autoClose: 3000,
				hideProgressBar: false,
				closeOnClick: true,
				pauseOnHover: true,
				draggable: true,
				progress: undefined,
			});
		})
	}

	const checkSignUp = () => {
		if(valueSignUp.username === '' || valueSignUp.password === '' || valueSignUp.email === '') {
			toast("Bn phi nhp y  thng tin!", {
				position: "top-right",
				autoClose: 3000,
				hideProgressBar: false,
				closeOnClick: true,
				pauseOnHover: true,
				draggable: true,
				progress: undefined,
			});
		} 
		else {
			authenServices.signUp(valueSignUp).then(res => {
				if(res.status===200) {
					toast("To ti khon thnh cng!", {
						position: "top-right",
						autoClose: 3000,
						hideProgressBar: false,
						closeOnClick: true,
						pauseOnHover: true,
						draggable: true,
						progress: undefined,
					});
				}
			}).catch(err => {
				toast(err.response.data, {
					position: "top-right",
					autoClose: 3000,
					hideProgressBar: false,
					closeOnClick: true,
					pauseOnHover: true,
					draggable: true,
					progress: undefined,
				});
			})
		}
	}

	function handleNameSignUp(e) {
		setValueSignUp({...valueSignUp, username: e.target.value})
	}

	function handleEmailSignUp(e) {
		setValueSignUp({...valueSignUp, email: e.target.value})
	}

	function handlePasswordSignUp(e) {
		setValueSignUp({...valueSignUp, password: e.target.value})
	}

  return (
    <div className="login-page pt-1">
     <h2>ng nhp/ ng k</h2>
<div className="container" id="container">
	<div className="form-container sign-up-container">
		<form action="javascript:void(0);">
			<h1>To ti khon</h1>
			<input type="text" placeholder="Tn ti khon" value={valueSignUp.username} onChange={handleNameSignUp}/>
			<input type="email" placeholder="Email" value={valueSignUp.email} onChange={handleEmailSignUp}/>
			<input type="password" placeholder="Mt khu" value={valueSignUp.password} onChange={handlePasswordSignUp}/>
			<button onClick={() => checkSignUp()}>ng k</button>
		</form>
	</div>
	<div className="form-container sign-in-container">
		<form action="javascript:void(0);" >
			<h1>ng nhp</h1>
			<input value={valueAccount.username} type="texts" placeholder="Ti khon" onChange={handleChangeUsername}/>
			<input value={valueAccount.password} type="password" placeholder="Mt khu" onChange={handleChangePassword}/>
			<a>Qun mt khu</a>
			<button onClick={() => checkSignIn()}>ng nhp</button>
		</form>
	</div>
	<div className="overlay-container">
		<div className="overlay">
			<div className="overlay-panel overlay-left">
				<h1>Cho mng tr li!</h1>
				<p>ng nhp vi ti khon c nhn</p>
				<button className="ghost" id="signIn">ng nhp</button>
			</div>
			<div className="overlay-panel overlay-right">
				<h1>Cho bn!</h1>
				<p>Nhp ti khon v mt khu  bt u hnh vi chng ti</p>
				<button className="ghost" id="signUp">ng k</button>
			</div>
		</div>
	</div>
</div>

{/* <footer>
	<p>
		Created with <i className="fa fa-heart"></i> by our team
	</p>
</footer> */}


    </div>
  );
}

export default withRouter(LogIn);
